body {
  margin: 0;
  overflow-y: scroll;
  font-family: 'Poppins', sans-serif;
}

.pic1, .app{
  display: flex;
  justify-content: space-around;
  margin: auto;
}

pre {
  font-size: 7em;
}
.marks .land {
  fill: #f6fcbab0;
}
.marks .sphere {
  fill: rgba(88, 196, 247, 0.795);
}
.marks .interiors {
  fill: none;
  stroke: #0000002c;
}
.marks .graticules, .tick line {
  fill: none;
}
circle {
  fill: #ff0000;
  opacity: 0.3;
}

.tick text {
  fill: #635F5D;
  font-size: 0.5em;
}
.mark {
  fill: #4aafdd;
}
.mark1 {
  fill: #f355a4;
}
.axis-label {
  font-size: 0.5em;
  fill: #635F5D;
}

.app1a{
  display: flex;  
  padding: 30px 10px 30px 125px;
  align-items: center;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.feed_but{
  padding-left: 30px;
}

.app1{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.app1b{
  display: block;
  padding: 10px;
  width: 200px;
}
.input1,.input2{
  width: 400px;
}
.rate{
  color:darkgray;
  width: 30px;
  height: 30px;
}

.rate:hover{
  color: rgb(255, 196, 0);
  cursor: pointer;
}

.rate_active{
  color: rgb(255, 196, 0);
}

.drawer_ic{
  width: 25vw;
}

.app1_avatar{
  width: 400px;
  display: flex;
  justify-content: flex-end;
}

.drawer_header{
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

.drawer_items{
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  align-items: center;
}

.feed_but{
  padding-left: 130px;
}